<template>
	<section class="cc_sction">
		<div class="close_btn_box"><span class="iconfont cc_font_cs" @click="closeHandler">&#xe638;</span></div>
		<article>
			<div class="cc_c_g">
				<div class="cc_container_top c"><p>No.19851122</p><span>未审核</span></div>
				<div class="cc_container_pic">
					<img src="./../assets/images/test_001.png" alt="">
				</div>
				<p class="cc_sche_name">WPT National China 中国（三亚）扑克游戏锦标赛</p>
			</div>
			<div class="cc_c_b">
				<div class="cc_b_pic">
					<img src="./../assets/images/test_001.png" alt="">
				</div>
				<p class="cc_c_uname">Weidade Zhuxi Xijinping</p>
				<p>用户昵称至多10个汉字</p>
				<p class="cc_c_timeline"><span>有效期截止：</span><time>2017.07.11 17:30</time></p>
			</div>
		</article>
		<p class="cc_tips">请扫描上方二维码进行检录</p>
	</section>
</template>

<script>
	export default {
		data(){
			return {
				next:''
			}
		},
		mounted () {
		    this.fetchData()
	  	},
	  	watch:{
	  		'$router':'fetchData'
	  	},
		methods:{
			closeHandler(){
				if(this.next){
					this.$router.push({
						name:this.next
					})
				}
			},
	    	fetchData(){
	    		this.next = this.$router.currentRoute.params.next;
	    	}
		}
	}
</script>

<style scoped lang="scss">
	@import './../assets/sass/function.scss';
	.cc_sction{
		overflow:hidden;
		border-bottom:1px solid transparent;
		article{
			margin:0 auto;
			width:px2em(640px);
			background:5px;
			border-radius:6px;
			overflow:hidden;
		}
	}
	.close_btn_box{
		width:px2em(690px);
		height:34px;
		margin:12px auto;
		span{
			float:right;
			width:34px;
			font-size:28px;
		}
	}
	.cc_c_g{
		background:#ccdfe0;
		padding-bottom:px2em(178px);
	}
	.cc_container_top{
		padding-top:5px;
		color:#3ea88d;
		p{
			float:left;
			margin-left:10px;
		}
		span{
			padding:2px 5px;
			float:right;
			margin-right:10px;
			border:1px solid #3ea88d;
			border-radius:2px;
		}
	}
	.cc_container_pic{
		position:relative;
		margin:px2em(74px) auto px2em(32px);
		width:px2em(320px);
		height:px2em(320px);
		background:#ffffff;
		img{
			position:absolute;
			top:0;left:0;right:0;bottom:0;
			margin:auto;
			width:90%;
			height:90%
		}
	}
	.cc_sche_name{
		margin:0 auto;
		width:px2em(520px);
		text-align:center;
		color:#3ea88d;
	}
	.cc_c_b{
		padding-bottom:5px;
		background:#3ea88d;
		text-align:center;
		color:#afeedf;
		border-top:1px solid #eee;
	}
	.cc_b_pic{
		position:relative;
		width:px2em(224px);
		height:px2em(224px);
		margin:px2em(-112px) auto 0;
		background:#ffffff;
		border-radius:50%;
		img{
			position:absolute;
			top:0;left:0;right:0;bottom:0;
			margin:auto;
			width:90%;
			height:90%;
			border-radius:50%;
		}
	}
	.cc_c_uname{
		color:#ffffff;
		font-size:px2em(30px);
		line-height:2;
	}
	.cc_c_timeline{
		margin-top:30px;
	}
	.cc_font_cs{
		color:#717989;
	}
	.cc_tips{
		padding:20px 0 10px;
		text-align:center;
		font-size:px2em(30px);
		color:#ffffff;
	}
</style>


